.App-header {
  /*background-color: #282c34;*/
  /*min-height: 100vh;*/

  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 1px 10px 18px 7px #000;
}
.App-header img{
  width: calc(30vmin);
}
.App main {
  max-width: 1200px;
  margin: 0 auto;
  padding-top:  5vmin;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: calc(5px + 2vmin);
  text-align: center;
  font-size: calc(10px + 2vmin);
  color: white;

}
.Rodada {
  display: grid;
  grid-template-columns: 2fr 1fr;
  width: calc(100px + 20vmin);
  
  column-gap: calc(5px + 2vmin);
 
}
.Rodada button{
  font-size: 100%;
}

.Rodada-escolha{
  display: flex;
  flex-direction: column;
  
}
.Rodada-escolha button{
  font-size: 100%;
  flex-grow: 1;
}
.Rodada-count {
  display:flex;
  align-items: center;
  justify-content: center;
  border-style: solid;
  border-width: 0.3vmin;
  border-color: #c7c3cf;
  border-radius: 2px;
  font-size: calc(22px + 5vmin);
  
}
.Reserva {
  display: grid;
  justify-content: center;
  
  width: calc(100px + 20vmin);
  border-style: solid;
  border-width: calc(0.3vmin);
  border-color: #c7c3cf;
  border-radius: 2px;
  row-gap: calc(5px + 2vmin);
}
.Reserva button{
  border: none;
  background-color:transparent;
  color: #c7c3cf;
}
.Reserva-movel{
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 2fr 1fr ;
  width: calc(100px + 20vmin);
  column-gap: 20px;
  font-size: calc(40px + 5vmin);
  align-items: center;
}
.Reserva-movel button{
  font-size: calc(50px + 3vmin);
}
.Reserva-conquista{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  border-style: solid solid none solid;
  border-width: calc(0.3vmin);
  column-gap: 5px;
  font-size: calc(20px + 3vmin);
  text-align: center;
  align-items: center;
}
.Reserva-conquista button{
  font-size: calc(20px + 3vmin);
}
.Fortaleza {
  display: grid;
  justify-content: center;
  grid-template-rows: 1fr 2fr 0.7fr 0.7fr; 
  row-gap: 3px;
   width: calc(100px + 20vmin);
  border-style: solid;
  border-width: calc(0.3vmin);
  border-color: #c7c3cf;
  border-radius: 2px;

  
}
.Fortaleza button{
  border: none;
  background-color:transparent;
  color: #c7c3cf;

}
.Fortaleza-Vida{
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 2fr 1fr ;
  width: calc(100px + 20vmin);
  column-gap: 20px;
  font-size: calc(40px + 5vmin);
  align-items: center;
  
}
.Fortaleza-Vida button{
  font-size: calc(50px + 3vmin);
}
.Aviso {
  display: grid;
  font-size: calc(6px + 2vmin);
  justify-content: center;
  align-items: center;
  
}
.Compra {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr 1fr ;
  align-items: center;
  padding-bottom: 2px;
}
.Compra button {
  font-size: calc(12px + 2vmin);
  
}
.Compra-Ativa{
  animation-name: compra-ativa;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
}

@keyframes compra-ativa{
 
  0%{
    filter: drop-shadow(0 0 0 #ffff);
  }
  50%{
    filter: drop-shadow(0 0 5px #ffff);
  }
  100%{
    filter: drop-shadow(0 0 0 #ffff);
  }


}

/*deixar responsivo*/

@media (max-width: 300px) {
    #app {
        flex-direction: column;
    }
    #app main {
        margin-top: 30px;
        margin-left: 0;
    }
    
    #app aside {
        width: 100%;
    }
}
@media (max-height: 360px){
  .App main{
    flex-direction: row;
    column-gap: calc(10px + 2vmin);
  }

}